.nav {
  background-color: white;
  flex-grow: 1;
  direction: rtl;
  box-shadow: 0 6px 6px 0 rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 0;
  z-index: 5;
  width: 100%;
  cursor: pointer;
}
.nav .logo {
  width: 130px;
  height: 60px;
}
.nav .navText {
  display: inline-block;
  font-size: 15px;
  margin-left: 0px;
}
.nav .join {
  margin-right: 10px;
  font-size: 15px;
  display: inline-block;
}
.nav .regBtn {
  background-color: #AF151F;
  color: white;
  margin-right: 12px;
  width: fit-content;
  font-size: 12px;
  padding: 6px 15px;
  min-width: auto;
}
.nav .regBtn:hover {
  background-color: #AF151F;
}
.nav .signInBtn {
  border-color: #AF151F;
  color: #AF151F;
  margin-right: 1rem;
  font-size: 12px;
  padding: 6px 12px;
  width: fit-content;
  min-width: auto;
}
.nav .signInBtn:hover {
  background-color: white;
  border: 1px solid #AF151F;
}
.nav .cart {
  font-size: 25px;
}

